import { type Writable, writable } from 'svelte/store';
import createController, { FormOptions, SubscribeState } from '@kensho/form';

type SubscribeHandler<S> = (state: S) => void;

interface Store<T> {
  subscribe: Writable<T>['subscribe'],
  set: Writable<T>['set'],
}

function createStore<T>(def?: T): Store<T> {
  const { subscribe, set } = writable(def);
  return {
    subscribe,
    set
  };
}

// export function createAdapter<S>(subscriber: (fn: SubscribeHandler<S>) => () => void): Store<S> {
//   const store = createStore({} as S);
//   subscriber(store.set);
//   return store;
// }

export function useKensho<T extends Record<string, unknown>, F extends boolean = false>(options: FormOptions<T, F>) {
  const ctrl = createController(options);
  const store = createStore({} as SubscribeState<T,F>);
  ctrl.subscribe(store.set);
  return {
    ...ctrl,
    store
  };
}